html {
  -webkit-overflow-scrolling: touch;
}

body {
  width: 100vw; height: 100vh;
  overflow: hidden;
  background-color: #222;
  margin: 0; padding: 0;
}

#root {
  width: 100%; height: 100%;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #ddd;
}

a:hover {
  color: #888;
}
